import { useState } from "react";
import { useDispatch } from "react-redux";
import { useHistory } from "react-router";
import { userService } from "../../services/userService";
import { signup } from "../../store/actions/userActions";

export const SignUp = () => {
  const [userCred, setUserCred] = useState(null);
  const dispatch = useDispatch();
  const history = useHistory();

  const handleChange = async ({ target }) => {
    const field = target.name;
    const value = target.value === "number" ? +target.value : target.value;
    setUserCred({ ...userCred, [field]: value });
  };

  const onSignUp = (ev) => {
    ev.preventDefault();
    dispatch(signup(userCred))
      .then(() => {
        history.push("/");
      })
      .catch((err) => {
        console.log(err.response.data);
      });
  };

  const onIsEmailSigned = async (ev) => {
    const inputEmail = ev.target.value;
    const isSigned = await userService.isSigned(inputEmail);
    console.log({isSigned});
  };

  return (
    <form onSubmit={onSignUp}>
      <h1>Signup</h1>
      <label htmlFor="fullname">Full name</label>
      <input
        required
        type="text"
        id="fullname"
        onChange={handleChange}
        name="fullname"
        placeholder="Ex.. Israel Israeli"
      />

      <label htmlFor="password">Password</label>
      <input
        required
        type="password"
        id="password-login"
        onChange={handleChange}
        name="password"
        placeholder="Ex.. !#@fdsf54%"
      />
      {/* <div className="form-email"> */}
      <label htmlFor="email">Email</label>
      <input
        required
        type="text"
        id="email-login"
        onChange={handleChange}
        onBlur={onIsEmailSigned}
        name="email"
        placeholder="Ex.. israeli2021@gmail.com"
      />
      {/* <img src="" alt="" />
        <img src="" alt="" /> */}
      {/* </div> */}
      <label htmlFor="phone">Phone number</label>
      <input
        required
        type="text"
        id="phone"
        onChange={handleChange}
        name="phone"
        placeholder="Ex.. 05x-xxxxxxx"
      />
      <button>Sign up</button>
      <p onClick={() => history.push("/signup/login")}>
        Already registered? login
      </p>
    </form>
  );
};
