import { transferService } from "../../services/transferService";

export const loadTransfers = () => {
  return async (dispatch) => {
      const transfers = await transferService.getTransfers();
    dispatch({ type: "SET_TRANSFERS", transfers });
  };
};

export const sendCoins = (contactEmail, amount) => {
  return async (dispatch) => {
    const transfer = await transferService.addTransfer(contactEmail, amount);
    dispatch({ type: "ADD_TRANSFER", transfer });
  };
};


